<template>
  <div class="output-pane" ref="outputPane">
    <pane-header bgColor="" title="">
      <span>
        <i class="iconfont icon-qingkong" title="" @click="clearOutput"></i>
        <el-checkbox v-model="clearAuto" class="clear-auto"></el-checkbox>
      </span>
    </pane-header>
    <div class="output-container" ref="outputContainer">
      <span v-for="(item, index) in currentOutput" 
            :key="index" 
            v-html="item" 
            class="output-data" >
      </span>
    </div>
  </div>
</template>

<script>
import {mapGetters,mapMutations} from 'vuex'
import paneHeader from 'components/pane-header'

export default {
  data () {
    return {
      scrollTopMap: {}
    }
  },
  methods: {
    clearOutput() {
      const index = this.currentIndex
      const content = ""
      const isRemove = true
      this.setOutputData({index,content,isRemove})
    },
    ...mapMutations({
      setOutputData :"SET_OUTPUT_DATA",
      setClearOutputAuto: "SET_CLEAR_OUTPUT_AUTO"
    })
  },
  watch: {
    currentOutput() {
      this.$nextTick(() => {
        let _this = this
        let scrollTop = this.scrollTopMap[this.currentIndex]
        const container = this.$refs.outputContainer
        const height=container.scrollHeight
        const top= scrollTop ? scrollTop : container.scrollTop
        const clientHeight = this.$refs.outputContainer.clientHeight
        if(top+clientHeight==height){   //
          container.scrollTop = top
          return
        }
        for(let i=0;i<height-top;i++){   //,
          container.scrollTop = top  //0Map
          setTimeout(function(){
            container.scrollTop++
            _this.scrollTopMap[_this.currentIndex] = container.scrollTop 
          },1*i)
        }
      })
    }
  },
  computed: {
    clearAuto: {
      get() {
        return this.clearOutputAuto
      },
      set(val) {
        this.setClearOutputAuto(val)
      }
    },
    ...mapGetters([
      "currentOutput",
      "currentIndex",
      "clearOutputAuto"
      ])
  },
  components: {
    paneHeader
  }
}
</script>

<style lang="scss" scoped>
  .output-pane-title {
    width: 100%;
    text-align: center;
    height: 23px!important;
    line-height: 23px!important;
  }
  .tag-data {
    height: 18px;
    line-height: 16px;
  }
  .output-pane /deep/{
    height: 100%;
    .show-time {
      float: right;
      font-size: 12px;
      color: #909399;
      margin-right: 10px;
    }
    .fail-data {
      background-color: #f2dede;
      color: #a94442;
    }
    .icon-bianyi {
      color: #909399!important;
    }
  }
  .output-container{
    height: calc(100% - 32px);
    margin-top: 5px;
    overflow: auto;
  }
  .output-data{
    color:#606266;
    margin-bottom: 5px;
    display: block;
  }
  .output-menu {
    position: absolute;
    right: 20px;
    font-size: 13px;
    line-height: 23px;
    cursor: pointer;
  }
  .output-clear:active {
    font-size: 15px;
  }
  .clear-auto /deep/{
    margin-left: 20px;
    color: white;
    .el-checkbox__label{
      color: white;
      padding-left: 3px;
    }
  }
</style>


