<template>
  <el-container>
      <el-header height="10" class="login-header">
          <img src="./Anyview.svg">
          <canvas  id="canvas"></canvas>
      </el-header>
      <el-main class="login-content">
        <div>
        <el-form label-width="100px" :model="user" status-icon :rules="rules" ref="user">
          <el-form-item label=""  prop="username">
            <el-input type="username" placeholder="" v-model="user.username"></el-input>
          </el-form-item>
          <el-form-item label=""  prop="password" >
            <el-input type="password"  placeholder="" v-model="user.password" show-password></el-input>
          </el-form-item>
          <el-form-item label="" class="school-select" prop="schoolId">
            <el-select v-model="user.schoolId" placeholder=""  filterable>
                  <el-option v-for="(school,index) in schoolList" :key="index" :value="school.id" :label="school.schoolName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item class="btn">
            <el-button type="primary" @click="login('user')">
              <span v-if="login_status"><i class="el-icon-loading"></i></span>
              <span v-else></span>
            </el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" style="width:100%"></el-button>
          </el-form-item>
          <el-form-item>
             <el-checkbox v-model="user.switch"></el-checkbox>
          </el-form-item>
        </el-form>
        </div>
      </el-main>
      <el-footer>
        <span></span>
        <span></span>
        <span></span>
        <span><a href="">Anyview</a></span>
      </el-footer>
  </el-container>
</template>

<script>
 import canvas from 'common/js/canvas';
 import {setCache,getCache} from 'common/js/cache';
 import {getSchoolMsg,getLoginMsg} from '@/api/login'
 import * as types from '@/api/config'

window.onresize = ()=>{canvas(20,"#409EFF")}
export default {
  data () {
    return {
      rules: {
        username: [
          { required: true, message: ''},
          { type: 'string', message: ''}
        ],
        password: [
          { required: true, message: ''}
        ],
        schoolId: [
            { required: true, message: '', trigger: 'change' }
        ],
                
      },
      schoolList:[],
      login_status: false,
      user: {
        roleId: 3,
        username: '',
        password: '',
        schoolId: '',
        switch: true
      }
    }
  },
  mounted () {
    this.$message({
          message: '',
          center: true,
          type: 'warning'
        })
      canvas(20,"#409EFF")   //canvas
      this._getSchoolist()
    },
  methods: {
      _input(c) {
        console.log(c)
      },
      _getSchoolist(){
        if(this.schoolList.length!=0) return
        getSchoolMsg('').then((e)=>{
          if(e.type === types.SCHOOL_SUCCESS_TYPE){
            this.schoolList=e.content
          }
        })
      },
      _getUser(){
        let user=this.user
        getLoginMsg(user).then((e)=>{
          this.login_status = false
          if(e.type === types.LOGIN_SUCCESS_TYPE){
            setCache("user",JSON.stringify(e.content))
            this.changeRoute('work')
          }
          else if(e.type === types.LOGIN_ALREADY_TYPE){
            this.messagePrompt("")
          }else{
            this.messagePrompt(e.content)
          }
        })
      },
      changeRoute(routeName){
        this.$router.replace(routeName)
      },
      messagePrompt(content){
        this.$message({
          showClose: true,
          message: content,
          duration: 1500,
          type: 'error'
        });
      },
      login (name) {
        this.$refs[name].validate((valid) => {
          if(valid) {
            this.login_status = true
            this._getUser()
          }
        });
      }

  }
}
</script>

<style>
   @import "../../assets/css/base.css";
</style>

<style lang="scss" scoped>
  .login-header{
     height: 30%;
     position: relative;
    text-align: center;
  }
  #canvas{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .login-content /deep/{
    width: 500px;
    margin: 0px auto;
    overflow: visible!important;
    .el-form{
      margin: 50px 80px 0 0;
    }
    .el-select{
      display: block;
    }
    .el-button{
      width: 100%;
    }
    .el-checkbox{
      float: right;
    }
  }
</style>


