<template>
  <div class="container">
    <h1 style="color:#303133"></h1>
    <el-card class="box-card card">
    <div slot="header" class="header">
      <span style="color:#409EFF"></span>
    </div>
    <div>
      <span
        v-for="(question,idx) in []" 
        :key="idx" >
        <el-popover
          placement="top-start"
          title=""
          width="200"
          trigger="hover"
          content=",,,">
          <el-button plain 
            :type="idx==1?'info':idx%2==0?'success':'danger'"
            class="question">{{question.name}}</el-button>
        </el-popover>
      </span>
    </div>
   </el-card>
    <el-card class="box-card card" v-for="(chap,index) in list" :key="index+1">
    <div slot="header" class="header">
      <span>{{`${chap[0]?chap[0].chapName:''}`}}</span>
      <span>
        <el-tag  effect="plain"></el-tag>
        <el-tag  effect="plain"></el-tag>
      </span>
    </div>
    <div>
      <span 
        v-for="(question,idx) in chap" 
        :key="idx" >
        <el-popover
          :open-delay="0"
          :close-delay="0"
          placement="bottom-start"
          width="200"
          trigger="hover">
          <p>: {{question.cmpErrorCount||0}}</p>
          <p>: {{question.cmpRightCount||0}}</p>
          <p>: {{question.runErrCount||0}}</p>
          <p v-if="question.firstPastTime">: {{formatDate(question.firstPastTime)}}</p>
          <el-button plain 
            :type="currentStatus(question)"
            class="question"
            slot="reference"
            @click="getContent(question)">{{question.name}}</el-button>
        </el-popover>
      </span>
    </div>
   </el-card>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      default: ()=>[]
    }
  },
  methods: {
    formatDate(now) {
      now = new Date(now)
      var year=now.getFullYear();  //4
      var month=now.getMonth()+1;  //011112
      var date=now.getDate();      //131
      var hour=now.getHours();     //023
      var minute=now.getMinutes(); //059
      var second=now.getSeconds(); //059
      return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second; 
    },
    getContent(question) {
      this.$emit("getContent",question)
    },
  },
  computed: {
    currentStatus(){
      return function (question) {
        return question.firstPastTime?'success':question.runErrCount?'danger': 'info'
      }
    }
  },
}
</script>

<style lang="scss" scoped>
  .container{
    display: flex;
    flex-direction: column;
    width: 63%;
    margin: 0 auto;
  }
  .card{
    margin: 30px;
  }
  .header {
    display: flex;
    justify-content: space-between;
  }
  .question{
    margin-right: 30px;
    margin-bottom: 30px;
  }
</style>


