<template>
    <div>
        <el-row class="el-card box-card is-always-shadow per-header">
            <el-col :span="12" :offset="4" style="display:flex;justify-content: space-between;align-items:center">
                <h1 style="color: #409eff;margin:0">Anyview</h1>
                <span style="font-size:17px;"></span>
                <span style="font-size:17px;" @click= "changeRoute('work')"></span>
            </el-col>
            <el-col :span="6"></el-col>
        </el-row>
        <el-row :gutter="20">
            <el-col :span="4" :offset="4">
                <el-card class="box-card">
                <div slot="header" class="clearfix">
                    <img src="./images/touxiang.jpg" alt="" style="height:48px;width:48px;">
                    <span>{{user.name}}</span>
                     <el-button style="float: right; padding: 3px 0" type="text" @click="logout"></el-button>
                </div>
                <p>{{user.name}}</p>
                <p>{{user.email}}</p>
                <p>{{user.username}}</p>
                <p></p>
                </el-card>
            </el-col>
            <el-col :span="12">
                <el-tabs type="border-card">
                    <el-tab-pane label="">
                    <div class="solve-trends">
                        <calendarHeatmap 
                        :values="[{ date: '2018-9-22', count: 6}]"
                        :end-date="2018-9-22"
                        :range-color="['ebedf0', 'dae2ef', '#c0ddf9', '#73b3f3', '#3886e1', '#17459e']"
                        style="margin-bottom: 20px;"
                        />
                        <el-timeline>
                            <el-timeline-item :timestamp="item.time" placement="bottom" v-for="(item,index) in trends" :key="index">
                                <div>
                                    <span>{{user.name}}</span>
                                    <span class="per-question-name" @click="goTo('Coding',item.name)">{{item.chapName+item.name}}</span>
                                    <span class="el-icon-check" style="color:#08bf91;">{{item.rightNum}}</span>
                                    <span class="el-icon-close" style="color:#dc3545;">{{item.errorNum}}</span>
                                </div>
                                    <el-button type="danger" round size="mini" v-if='item.errorNum!=0'></el-button>
                                    <el-button type="success" round size="mini" v-else></el-button>
                            </el-timeline-item>
                        </el-timeline>                                              
                    </div>
                    </el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                    <el-tab-pane label=""></el-tab-pane>
                </el-tabs>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="6"></el-col>
            <el-col :span="18">
            </el-col>
        </el-row>
        
    </div>
</template>

<script>
 import { CalendarHeatmap } from 'vue-calendar-heatmap'
 import {getLogout} from '@/api/work'
 import {setCache,getCache,removeCache,clearSessionCache} from 'common/js/cache';
 import * as types from '@/api/config'
 import {mapMutations} from 'vuex'

export default {
    created(){
    },
    data() {
        return {
        }
    },
    methods: {
      changeRoute(routeName){
        this.$router.replace(routeName)
      },
      logout() {
        getLogout(this.user).then((e)=>{
          if(e.type === types.LOGOUT_SUCCESS_TYPE){
            console.log("",e)
            this.changeRoute("login")
            removeCache("user")
            clearSessionCache()
            this.resetState()    //vuex
          }
        })
      },
      ...mapMutations({
          resetState: 'RESET_STATE'
      })
    },
    computed: {
      user() {
        return JSON.parse(getCache("user"))
      },
      trends(){
        return this.$store.state.solveTrends
      }
    },

    components: {
        CalendarHeatmap
    }

}
</script>

<style>
   @import "../../assets/css/base.css";
</style>

<style lang="scss" scoped>
.el-row{
    margin-top: 20px;
}
.per-question-name{
    cursor: pointer;
    font-size: 13px;
    color: rgb(69, 90, 100);
}
.per-question-name:hover{
    text-decoration:underline;
}
.per-header{
    margin-top: 0;
    padding: 5px;
}
.per-header span{
    cursor: pointer;
}
.per-header span:hover{
    color:#409EFF;
}
.solve-trends{
    padding: 10px;
}
  .solve-trends span{
    padding: 0 10px;
  }
  .solve-trends .el-timeline-item__content{
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
  }
  .solve-trends .is-round{
      padding: 4px!important;
  }
</style>


