/*

 */
import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from '../pages/Login/Login.vue'
import work from '../pages/Work/Work.vue'
import Coding from '../pages/Coding/Coding.vue'
import PersonalCenter from '../pages/PersonalCenter/PersonalCenter.vue'
// 
Vue.use(VueRouter)

export default new VueRouter({
  mode: 'history',    //history#
  // 
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login, // , , 
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/work',
      name: 'work',
      component: work,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/coding',
      name: 'coding',
      component: Coding,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/PersonalCenter',
      name: 'personalCenter',
      component: PersonalCenter
    },
    {
      path: '/',
      component: Login,
      redirect: '/login'   //
    },
    {
      path: '**',   // 
      redirect: '/login'   //
    },
  ]
})



