import { resolve } from "url";
import { rejects } from "assert";

let websocket = null;
const IP = '101.132.120.137'  
// 101.132.148.120. 139.159.186.173 101.132.120.137
let port = '8889';	//webSocket
let close=false
let backData = true
function initWebSocket(){ //websocket
    var url = `ws://${IP}:${port}/anyview`;
    websocket = new WebSocket(url);
    websocket.onclose = function(e){
        close=true
        const ifReload = window.confirm("?");
        ifReload? window.location.reload(): ''
        console.log('websocket : ' + e.code + ' ' + e.reason + ' ' + e.wasClean)
    }
	websocket.onerror = function () {
		console.log("WebSocket");
    }
}

//
async function sendMsg(data){
    if(websocket.readyState===1){    //1websocket
        console.log('send',data)
        websocket.send(JSON.stringify(data),data)
        return
    }
    await new Promise((resolve,reject)=>{  //
        websocket.onopen = function () {
            console.log("");
            resolve()
        }
    })
    console.log('send',data)
    websocket.send(JSON.stringify(data),data)
    // console.log(websocket.readyState)
    // if (websocket.readyState === websocket.OPEN) {
    //     //ws
    //     console.log("send",data)
    //     websocket.send(JSON.stringify(data),data)}
    // }else {
    //     //  1s
    //     if(!close)
    //     setTimeout(()=>{
    //         sendMsg(data)
    //     }, 1000);
    // }
}

//
function getMsg(){
    return new Promise((resolve,reject)=>{
        websocket.onmessage=(e)=>{
            backData=true
            resolve(JSON.parse(e.data))
        }
    })
}
 
initWebSocket();
 
export{sendMsg,getMsg}


