<template>
  <div ref="dp" style="position:absolute;z-index:10">
    <el-card class="debug-menu" v-if="debugStatus">
      <span class="debug-title"><i class="iconfont icon-_tiaoshi" title="" style="cursor:move" ref="dpt"></i></span>
      <ul style="display:inline-block">
        <li><i class="iconfont icon-xiayibu clickIcon" title="()(F11)" @click= "stepInto"></i></li>
        <li><i class="iconfont icon-xiayihang clickIcon" title="()(F10)" @click= "stepOver"></i></li>
        <li><i class="iconfont icon-jixutiaoshi clickIcon" title="(F9)" @click= "continueDebug"></i></li>
        <li><i class="iconfont icon-tingzhitiaoshi clickIcon" title="(shift+F9)" @click= "quitDebug"></i></li>
      </ul>
    </el-card>
  </div>
</template>

<script>
import {mapGetters,mapMutations,mapActions} from 'vuex'
import * as fun from '@/api/coding'
import * as types from '@/api/config'
import {analyseToVisual} from 'common/js/analyseToVisual.js'
import {DebugReq} from 'common/js/class'
export default {
  created () {
  },
  mounted() {
    let that = this
    this._timer=setTimeout(()=>{
      //
      var dv = that.$refs.dp;
      var dpt = this.$refs.dpt
      var x = 0;
      var y = 0;
      var l = 0;
      var t = 0;
      var isDown = false;
      //
      dpt.onmousedown = function(e) {
          //xy
          x = e.clientX;
          y = e.clientY;
          //
          l = dv.offsetLeft;
          t = dv.offsetTop;
          //
          isDown = true;
          //  
          dv.style.cursor = 'move'
      }
      //
      window.onmousemove = function(e) {
          if (isDown == false) {
              return;
          }
          //xy
          var nx = e.clientX;
          var ny = e.clientY;
          //
          var nl = nx - (x - l);
          var nt = ny - (y - t);
        
          dv.style.left = nl + 'px';
          dv.style.top = nt + 'px';
      }
      //
      dpt.onmouseup = function() {
          //
          isDown = false;
          dv.style.cursor = 'default';
      }
    })    
  },
  beforeDestroy(){
    clearTimeout(this._timer)
  },
  props: {
    renderVariate: {
      type: Array,
      default: () => []
    },
  },
  data () {
    return {
      status: true    //
    }
  },
  methods: {
    stepOver() {
      if(!this.status)
        return
      this.status =false
      const content = {eID: this.currentIndex,cmd:"next\n"}
      fun.getStepOverMsg(content).then((e)=>{
        if(types.STEP_OVER_SUCCESS_TYPE == e.type) {
          // analyseToVisual(e)
          this.status = true
          if(e.content.output.includes("")) {
            this.commitStopDebug(e)
          }
          this.commitDebugData(e)
        }
      })
    },
    stepInto() {
      if(!this.status){
        return
      }
      this.status =false
      const content = {eID: this.currentIndex,cmd: "step\n"}
      fun.getStepIntoMsg(content).then((e)=>{
        if(types.STEP_INTO_SUCCESS_TYPE == e.type) {
          console.log('into',e)
          this.status = true
          if(e.content.output.includes("")) {
            this.commitStopDebug(e)
          }
          this.commitDebugData(e)
        }
      })
    },
    commitStopDebug(e) {
      this.setStatus({type:"debugStatus",status:false}).then(()=>{
        let style = "warning"
        let label = ""
        const index = this.currentIndex
        this.setOutput({style, label})
        if(Object.keys(e.content.errorOrder).length===0&&this.initTestDataLength!=0){   //
          style = 'success'
          label = ''
          this.setOutput({style, label})
        }
        this.setDebugData({index})
        this.setCurrentTestOrder(-1)
      })
    },
    continueDebug() {
      if(!this.status)
        return
      this.status =false
      const content = {eID: this.currentIndex,cmd: "continue\n"}
      fun.getContinueDebugMsg(content).then((e)=>{
        if(types.CONTINUE_DEBUG_SUCCESS_TYPE == e.type) {
          console.log('continue',e)
          this.status = true
          if(e.content.output.includes("")) {
            this.commitStopDebug(e)
          } 
          this.commitDebugData(e)    
        }
      })
    },
    quitDebug() {
      if(!this.status)
        return
      this.status =false
      const content = {eID:this.currentIndex}
      fun.getQuitDebugMsg(content).then((e)=>{
        if(types.QUIT_DEBUG_SUCCESS_TYPE == e.type){
          console.log('',e)
          this.status = true
          this.commitStopDebug(e)
        }
      })
    },
    commitDebugData(e) {
      if(e.content.exception){
        this.setOutput({
                        style: "danger",
                        label: "",
                        _content: e.content.output
        })
        return
      }
      const index = this.currentIndex
      const _obj = {}
      _obj.lineNum = e.content.lineNum
      _obj.variate = e.content.variate
      _obj.dataStruct = e.content.dataStruct
      _obj.watchPoint = e.content.watchPoint
      _obj.backTrace = e.content.backTrace
      _obj.output = e.content.output
      this.setDebugData({index,_obj})
      if(e.content.output)
        this.setOutput({
                        style: "",
                        label: "",
                        _content: e.content.output
                      })
      this.setCurrentTestOrder(e.content.order+1)
      this.setErrorTestData({data:e.content.errorOrder,id:this.currentIndex})  //
    },
    ...mapActions([
      'setStatus',
      'setOutput'
    ]),
    ...mapMutations({
      setCurrentQuestion: 'SET_CURRENT_QUESTION',
      setCurrentIndex: 'SET_CURRENT_INDEX',
      setOpenQuestions: 'SET_OPEN_QUESTIONS',
      setDebugData: "SET_DEBUG_DATA",
      setErrorTestData: "SET_ERROR_TEST_DATA",
      setCurrentTestOrder: "SET_CURRENT_TEST_ORDER"
    }) 
  },
  watch: {
    debugStatus() {
      var _this = this
    document.onkeydown=function(e){
        var e=e||window.event
        const keyCode = e.keyCode
        if(keyCode==122||keyCode==121||keyCode==120){ 
          e.stopPropagation();//
          e.preventDefault(); //
        }
   if(keyCode == 122)
          _this.stepInto()
        else if(keyCode == 121)
          _this.stepOver()
        else if(keyCode == 120 && e.shiftKey)
          _this.quitDebug()
        else if(keyCode == 120)
          _this.continueDebug()
      }
    }
  },
  computed: {
    debugStatus() {
      return this.currentQuestion.debugStatus
    },
    ...mapGetters([
      "currentIndex",
      "currentQuestion",
      "currentDebug",
      "openQuestionsArr",
      "initTestDataLength"
    ]),
  },
}
</script>

<style lang="scss" scoped>
  .debug-menu {
    transition: all 1s;
    z-index: 50;
  }
  .debug-menu /deep/{ 
    .el-card__body{
      padding: 12px 20px;
    }
  }
  li:active{
    font-size: 19px;
    margin: -1.8px;
  }
  .debug-title {
    display: inline-block;
    height: 100%;
    padding: 0 5px 0 0;
    margin: -14px 10px -14px -5px;
    border-right: 3px dotted #909399;
  }
</style>


